.profile-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    background: linear-gradient(to right, #000080, #1084d0);
    padding: 12px;
    border: 2px solid;
    border-top-color: #ffffff;
    border-left-color: #ffffff;
    border-right-color: #404040;
    border-bottom-color: #404040;
    color: #ffffff;
}

.buddy-icon {
    width: 200px;
    height: 200px;
    margin-right: 20px;
    border: 2px solid;
    border-top-color: #808080;
    border-left-color: #808080;
    border-right-color: #ffffff;
    border-bottom-color: #ffffff;
    padding: 2px;
    background: #ffffff;
    object-fit: cover; /* Ensures the image is not crunched */
}

.screen-name {
    font-size: 24px;
    margin: 0;
    text-shadow: 1px 1px #000000;
    color: #ffffff;
}